<template>
  <div class="trend-food-list">
    <hooper class="frame">
      <slide
        class="slide"
        v-for="(slide, indx) in slides"
        :key="slide.id"
        :index="indx"
        ><div class="picture">
          <img :src="slide.image[0].image_url" alt="" />
        </div>
        <div class="card">
          <div class="icon">
            <img src="../assets/avatar.png" alt="" />
            <img class="like" :src="slide.icon" alt="" />
          </div>
          <div class="text">
            <p>Trending Food</p>
            <p class="name">{{ slide.food_name }}</p>
            <p>
              by <span>{{ slide.username }}</span>
            </p>
          </div>
        </div>
      </slide>
    </hooper>
  </div>
</template>

<script>
import { Hooper, Slide } from "hooper";
import axios from "axios";
import "hooper/dist/hooper.css";
export default {
  components: {
    Hooper,
    Slide,
  },
  data() {
    return {
      slides: [
        {
          id: 1,
          img:
            "https://s1.ig.meishij.net/p/20130423/723b559043e089b89ad7a058816c74fb.jpg",
          food_name: "Pineapple shrimp",
          user_icon: "../assets/avatar.png",
          user_name: "Simomo",
        },
        {
          id: 2,
          img:
            "https://recipeland.com/images/r/11752/af66d92ee33758daad5a_1024.jpg",
          food_name: "Penne-Carbonara",
          user_icon: "../assets/avatar.png",
          user_name: "Simomo",
        },
        {
          id: 3,
          img:
            "https://lh3.googleusercontent.com/proxy/uLfYc_MkAMD9QGn-GV73yz4HfusVFwLvR2R9sx4dCL4Y4PyMkv1NkWB7WSx6TdMYb5keRXWx0R9FOqOqXNyTluQlaW06EbB8M04kFudp7-mKNSLjWg",
          food_name: "红烧牛肉牛筋面",
          user_icon: "../assets/avatar.png",
          user_name: "Simomo",
        },
      ],
    };
  },
  methods: {
    getFoodList() {
      axios
        .request({
          url: "https://homedelicious.ml/api/foods",
          method: "get",
        })
        .then((response) => {
          console.log(response.data);
          this.slides = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
  mounted () {
      this.getFoodList();
  },
};
</script>

<style lang="scss" scoped>
@import "../assets/style/variable.scss";
.trend-food-list {
  box-sizing: border-box;
  .frame {
    width: 90%;
    height: 95%;
    margin: 5%;
    // background-color: rgb(202, 115, 115);
    .slide {
      position: relative;
      .picture {
        height: 80%;
        width: 70%;
        margin-left: 15%;
        overflow: hidden;
        img {
          height: 100%;
          width: 100%;
          object-fit: cover;
          border-radius: 10px;
        }
      }
      .card {
        box-sizing: border-box;
        padding: 3% 5%;
        background-color: #fff;
        height: 40%;
        width: 50%;
        position: absolute;
        bottom: 10%;
        left: 25%;
        border-radius: 20px;
        filter: $shadow;
        font-size: 0.8rem;
        font-family: $fonts;
        color: $fontColorlight;
        display: grid;
        grid-template-rows: 1fr 2fr;
        .icon {
          display: flex;
          justify-content: space-between;
          img {
            height: 3vh;
          }
          .like {
            height: 2vh;
          }
        }
        .name {
          color: black;
          font-size: 1rem;
        }
      }
    }
  }
}
</style>